

/* color help https://stackoverflow.com/questions/7415872/change-color-of-png-image-via-css */
.whitepic {
    filter:brightness(0) saturate(100%) invert(93%) sepia(2%) saturate(278%) hue-rotate(81deg) brightness(106%) contrast(93%);
}

.goldpic {
    filter:brightness(0) saturate(100%) invert(73%) sepia(48%) saturate(272%) hue-rotate(0deg) brightness(86%) contrast(86%);
}


.darkspot {
    color: #f1f5f2;
    background-color: #14281d;
}

a {
    color: #c2a878
}

p {
    color: #F1F5F2;
    margin-left: 2%;
    margin-right: 2%;
}

h1 {
    color: #f1f5f2
}